.backgroundContainer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: -2;
}

.darkBack {
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.3;
    z-index: -1;
    position: fixed;
}

.dark, .dark button {
    background-color: #0d1117 !important;
    color: #c9d1d9 !important;
}

.darkA a{
    color: #c9d1d9 !important;
}

.dark h1, .dark h2, .dark h3 , .dark .ant-tabs-tab-btn, .dark label {
    color: #c9d1d9 !important;
}

.dark .ant-tabs-tab-btn:hover, .dark .ant-tabs-tab-btn:focus {
    color: cornflowerblue !important;
}

.dark .ant-tabs-tab.ant-tabs-tab-disabled {
    opacity: 0.4;
}

.bordered {
    border-color: dimgray !important;
}

.background {
    width: 100%;
    height: 100%;
}

.ant-card-hoverable {
    cursor: default !important;
}

.ant-tabs-tab-btn {
    margin: 0 10px;
    font-weight: bold;
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn{
    font-weight: bold !important;
}